import styled, {css} from "styled-components";

const StyledButton = styled.button`
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: bold;
  padding: 0.25rem 1rem; /*   0.25rem,   1rem */
  color: white;
  outline: none; /* border   */
  cursor: pointer; /*         */
  background: #8BEDA4;

  ${props => props.fullWidth && css` /* fullWidth  true     */
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    width: 100%;
    font-size: 1.125rem;
  `}
`;

const Button = props => {
    return (
        <StyledButton {...props}/>
    );
};

export default Button;